import React, { useState } from "react";

import styled from "./QandA.module.scss";
import classNames from "classnames/bind";
import team from "../../components/Image/team.jpg";
import anhtan from "../../components/Image/anhtan2.jpg"
import AddTwoToneIcon from "@mui/icons-material/AddTwoTone";
function QandA() {
  const cx = classNames.bind(styled);

  const [activeItemOne, setActiveItemOne] = useState(true);
  const [activeItemTwo, setActiveItemTwo] = useState(true);

  const handleActiveOne = () => {
    setActiveItemOne(!activeItemOne);
    setActiveItemTwo(true);
  };
  const handleActiveTwo = () => {
    setActiveItemTwo(!activeItemTwo);
    setActiveItemOne(true);
  };

  return (
    <div className={cx("QandA")}>
      <div className="container">
        <div className={cx("flex")}>
          <div className={cx("content")}>
            <div className={cx("QandA-content")}>
              <h2>FAQs</h2>
              <p className={cx("QandA-content-p")}>
                IT-Global xin php gii p mt s thc mc chung ca Qu khch.
              </p>

              <div className={cx("list-QandA")}>
                <div className={cx("card")}>
                  <div className={cx("title")}>
                    <h4>Quy trnh pht trin phn mm ti It-Global</h4>
                    <AddTwoToneIcon
                      className={cx("icon")}
                      onClick={() => handleActiveOne()}
                    />
                  </div>
                  <div
                    className={cx(activeItemOne ? "listHiden" : "listActive")}
                  >
                    <p>1. Ly yu cu t khch hng</p>
                    <p>2. Phn tch - thit k h thng</p>
                    <p>3. Nhn phn hi t khch hng</p>
                    <p>4. Pht trin h thng</p>
                    <p>5. Nhn phn hi t khch hng</p>
                    <p>6. Hon thin h thng</p>
                    <p>7. Bo tr h thng.</p>
                  </div>
                </div>
                <div className={cx("card")}>
                  <div className={cx("title")}>
                    <h4>Cc cng ngh c IT-Global s dng ?</h4>
                    <AddTwoToneIcon
                      className={cx("icon")}
                      onClick={() => handleActiveTwo()}
                    />
                  </div>
                  <div
                    className={cx(
                      activeItemTwo ? "listHiden" : "listActiveTwo"
                    )}
                  >
                    <p>
                      1. Thit k website: HTML, CSS, JS, Laravel, Reactjs,
                      Vuejs, Nodejs, Angular,..
                    </p>
                    <p>
                      2. Lp trnh ng dng di ng: Java, React Native,
                      Fluter,..
                    </p>
                    <p>
                      3. Lp trnh phn mm ng dng Desktop: .Net Framework
                    </p>
                  </div>
                </div>
              </div>
              <div className={cx("maxim")}>
                <p>
                  "Chng ti lun n lc ht sc  mang li nhng tri nghim
                  hon ho, hiu qu trong cng vic cho Khch hng vi mt chi
                  ph ti u."
                </p>
              </div>

              <div className={cx("anhtan")}>
                <div className={cx("header")}>
                  <img src={anhtan} alt="" />
                  <div className={cx("user")}>
                    <h6>Nguyn Minh Tn</h6>
                    <p>Founder, CEO</p>
                  </div>
                </div>
              </div>
            </div>
            <div className={cx("img")}>
              <img src={team} alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default QandA;
